import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';

import * as api from './request';
import rootReducer from './reducer';
import * as actions from './reducer/actions';

import AppRouter from './routes';
import AppContext from './context';
import Footer from './components/Footer';
import MainNav from './components/MainNav';
import Modal from './components/Modal';
import ImagePreview from './components/ImagePreview';

class App extends React.Component {
  constructor (props) {
    super(props);

    this.dispatch = this.dispatch.bind(this);
    this.addCartItem = this.addCartItem.bind(this);
    this.handleRemove = this.handleRemove.bind(this);
    this.toggleImagePreview = this.toggleImagePreview.bind(this);

    this.state = {
      data: {
        products: [],
        cart: {
          id: '',
          lineItems: [],
        },
      },

      isPreviewImageShowing: false,

      dispatch: this.dispatch,
      addCartItem: this.addCartItem,
      handleRemove: this.handleRemove,
      toggleImagePreview: this.toggleImagePreview,
    }
  }

  componentDidMount () {
    this.fetchCart();
  }

  /**
   *
   * @param {object} action
   * @param {string} type
   * @param {any?}   action.payload
   */
  dispatch (action) {
    this.setState(prevState => ({
      data: rootReducer(prevState.data, action)
    }));
  }

  fetchCart () {
    let cartId = localStorage.getItem('cartId');

    if (!cartId) return

    api.getCart(cartId)
      .then((data) => {
        this.dispatch({
          type: actions.RECEIVE_CART,
          payload: {
            cart: data.cart,
          },
        });
      })
      .catch((error) => {
        localStorage.removeItem('cartId');
        throw error;
      });
  }

  toggleImagePreview () {
    this.setState(({ isPreviewImageShowing }) => ({
      isPreviewImageShowing: !isPreviewImageShowing,
    }));
  }

  handleRemove (productId) {
    let cartId = localStorage.getItem('cartId');

    this.dispatch({
      type: actions.REMOVE_LINE_ITEM,
      payload: {
        productId,
      },
    });
    return api.removeItem(cartId, productId);
  }

  addCartItem (product) {
    this.dispatch({
      type: actions.ADD_LINE_ITEM,
      payload: {
        product,
        qty: 1
      },
    });

    return new Promise((resolve, reject) => {
      let cartId = localStorage.getItem('cartId');

      if (cartId) {
        return resolve(cartId);
      }

      api.createCart()
        .then((data) => {
          cartId = data.cart.id;
          localStorage.setItem('cartId', cartId);

          resolve(cartId);
        })
        .catch(reject);
    })
      .then((cartId) => {
        return api.addItem(cartId, product)
      });
  }

  render () {
    const { isPreviewImageShowing } = this.state;

    return (
      <AppContext.Provider value={this.state}>
        <Router>
          <>
            <MainNav />
            <AppRouter/>
            <Footer />

            {isPreviewImageShowing && (
              <Modal>
                <ImagePreview onOverlayClick={this.toggleImagePreview} />
              </Modal>
            )}
          </>
        </Router>
      </AppContext.Provider>
    );
  }
}

export default App;
